import { spawn } from "child_process";
import path from "path";
import fs from "fs";

export default async function handler(req, res) {
    if (req.method === "POST") {
        try {
            const { videoUrl } = req.body;
            if (!videoUrl) {
                return res.status(400).json({ error: "No video URL provided." });
            }

            // Step 1: Download video
            const tempVideoPath = path.join(
                process.env.TEMP_DIR || "C:\\Users\\KIIT\\AppData\\Local\\Temp",
                `temp-video-${Date.now()}.mp4`
            );
            await downloadVideo(videoUrl, tempVideoPath);
            console.log("Video downloaded successfully:", tempVideoPath);

            // Step 2: Transcribe video with Whisper
            const tempDir = path.dirname(tempVideoPath);
            const whisperProcess = spawn("whisper", [
                tempVideoPath,
                "--model", "base",
                "--output_dir", tempDir,
                "--output_format", "json",
            ]);

            let whisperStdout = "";
            let whisperStderr = "";

            whisperProcess.stdout.on("data", (data) => {
                whisperStdout += data.toString();
            });

            whisperProcess.stderr.on("data", (data) => {
                whisperStderr += data.toString();
            });

            await new Promise((resolve, reject) => {
                whisperProcess.on("close", (code) => {
                    if (code === 0) {
                        resolve();
                    } else {
                        console.error("Whisper failed:", whisperStderr);
                        reject(new Error("Whisper transcription failed."));
                    }
                });
            });

            console.log("Whisper transcription completed:", whisperStdout);

            // Step 3: Read the generated JSON file
            const whisperOutputFile = path.join(tempDir, `${path.basename(tempVideoPath, ".mp4")}.json`);
            if (!fs.existsSync(whisperOutputFile)) {
                console.error(`Transcription file not found: ${whisperOutputFile}`);
                return res.status(500).json({ error: "Transcription file not generated." });
            }

            const transcriptData = fs.readFileSync(whisperOutputFile, "utf-8");
            const transcriptJson = JSON.parse(transcriptData);
            const transcript = transcriptJson.text || "No transcript available.";

            // Clean up temporary files
            fs.unlinkSync(tempVideoPath);
            fs.unlinkSync(whisperOutputFile);

            return res.status(200).json({ transcript });
        } catch (error) {
            console.error("Error during transcription:", error);
            return res.status(500).json({ error: "Internal server error." });
        }
    } else {
        return res.status(405).json({ error: "Method not allowed." });
    }
}

